.card {
    display: inline-block;
    background: #fff;
    border-radius: 2px;
    width: 230px;
    height: 200px;
    margin: 10px;
    position: relative;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }

  .img-container {
    display: inline-block;
    overflow: hidden;
    background-color: white;
    margin-right: 4px;
    margin-left: 4px;
  }

  .card > .img-container > img {
    width: 300px;
    height: 300px;
    display: inline-block;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
    }
  
  .newGame {
    -webkit-animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
            animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }

  /* Source: https://codepen.io/wifeo/pen/qzwkb */
  .style_prevu_kit
{
    display:inline-block;
    border:0;
    width:250px;
    height:220px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    transform: scale(1);   
}

.style_prevu_kit:hover
{
    cursor:pointer;
    -webkit-box-shadow: 0px 0px 150px #000000;
            box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.3);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.3);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.3);
    -o-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    transform: scale(1.3);
}

.style_prevu_kit_ex {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
  /* Source: https://css-tricks.com/snippets/css/shake-css-keyframe-animation/ */
  @-webkit-keyframes shake {
    10%, 90% {
      -webkit-transform: translate3d(-1px, 0, 0);
              transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      -webkit-transform: translate3d(2px, 0, 0);
              transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      -webkit-transform: translate3d(-4px, 0, 0);
              transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      -webkit-transform: translate3d(4px, 0, 0);
              transform: translate3d(4px, 0, 0);
    }
  }
  @keyframes shake {
    10%, 90% {
      -webkit-transform: translate3d(-1px, 0, 0);
              transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      -webkit-transform: translate3d(2px, 0, 0);
              transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      -webkit-transform: translate3d(-4px, 0, 0);
              transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      -webkit-transform: translate3d(4px, 0, 0);
              transform: translate3d(4px, 0, 0);
    }
  }


